'use strict';
angular.module('oneImobiliaria', ['ui.router', 'ui.utils.masks']).run([
  '$rootScope', '$state', 'RESOURCES', function($rootScope, $state, RESOURCES) {
    $rootScope.error = false;
    $rootScope.success = false;
    $rootScope.loading = false;
    $rootScope.page = '';
    $rootScope.showMenu = false;
    $rootScope.appTitle = 'One Consultoria ImobiliÃ¡ria';
    $rootScope.forms = {};
    $rootScope.toggleMenu = function() {
      return $rootScope.showMenu = !$rootScope.showMenu;
    };
    return $rootScope.$on('$stateChangeSuccess', function() {
      var page;
      page = $state.current.name.split('.');
      $rootScope.page = page[1] || '';
      $rootScope.error = false;
      $rootScope.success = false;
      $rootScope.loading = false;
      return $rootScope.showMenu = false;
    });
  }
]).constant('RESOURCES', {
  'API_URL': 'http://104.236.81.253:1337'
});

'use strict';
angular.module('oneImobiliaria').config([
  '$stateProvider', '$httpProvider', '$urlRouterProvider', function($stateProvider, $httpProvider, $urlRouterProvider) {
    $stateProvider.state('login', {
      url: '/login',
      views: {
        main: {
          templateUrl: 'app/views/login/index.html',
          controller: 'LoginCtrl'
        },
        'error@login': {
          templateUrl: 'app/views/error.html'
        }
      },
      requiredLogin: false
    }).state('dashboard', {
      url: '/dashboard',
      abstract: true,
      views: {
        main: {},
        header: {
          templateUrl: 'app/views/header.html'
        },
        footer: {
          templateUrl: 'app/views/footer.html'
        }
      },
      requiredLogin: true
    }).state('dashboard.home', {
      url: '/home',
      views: {
        'main@': {
          templateUrl: 'app/views/home/index.html'
        }
      },
      requiredLogin: true
    }).state('dashboard.users', {
      url: '/users',
      views: {
        'main@': {
          templateUrl: 'app/views/users/index.html',
          controller: 'UsersCtrl'
        },
        'submenu@dashboard.users': {
          templateUrl: 'app/views/users/submenu.html'
        }
      },
      requiredLogin: true
    }).state('dashboard.users.new', {
      url: '/new',
      views: {
        'main@': {
          templateUrl: 'app/views/users/form.html',
          controller: 'UsersCtrl'
        }
      },
      requiredLogin: true
    }).state('dashboard.properties', {
      url: '/properties',
      views: {
        'main@': {
          templateUrl: 'app/views/properties/index.html'
        },
        'submenu@dashboard.properties': {
          templateUrl: 'app/views/properties/submenu.html'
        }
      },
      requiredLogin: true
    });
    $urlRouterProvider.otherwise('/login');
    $httpProvider.defaults.useXDomain = true;
    $httpProvider.defaults.headers.common["Content-Type"] = 'application/json';
    return delete $httpProvider.defaults.headers.common['X-Requested-With'];
  }
]);

'use strict';
angular.module('oneImobiliaria').service('loading', [
  '$rootScope', function($rootScope) {
    return {
      show: function() {
        return $rootScope.loading = true;
      },
      hide: function() {
        return $rootScope.loading = false;
      }
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').service('UserGroupService', function() {
  var items;
  items = [
    {
      title: 'Administrador',
      code: 'admin'
    }, {
      title: 'Corretor(a)',
      code: 'broker'
    }
  ];
  return {
    getAll: function() {
      return items;
    }
  };
});

'use strict';
angular.module('oneImobiliaria').controller('LoginCtrl', [
  '$scope', '$state', function($scope, $state) {
    $scope.user = {};
    return $scope.doLogin = function() {
      return $state.go('dashboard.home');
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').controller('UsersCtrl', [
  '$scope', '$state', 'UserGroupService', function($scope, $state, UserGroupService) {
    $scope.user = {
      name: 'Thais Martins',
      email: 'contato@thaismartins.co',
      group: 'broker'
    };
    $scope.groups = UserGroupService.getAll();
    $scope.edit = false;
    $scope.canEdit = function() {
      return $scope.edit = true;
    };
    return $scope.saveOrUpdate = function() {
      return $state.go('dashboard.users');
    };
  }
]);
